<template>
  <div class="myChart" style="width: 300px;height: 300px">

  </div>
</template>

<script>


  export default {
    name: "my-chart",
    data() {
      return {}
    },
    mounted() {
      this.drawLine();
    },
    methods: {
      drawLine() {
        let dom = document.querySelector(`.${this.className}`)
        let myChart = window.echarts.init(dom)
        myChart.on("click", (item) => {
          let {Bus, actions} = this.$attrs
          Bus.$emit("message", item)
          actions.setGlobalState({data: "smt-vue"})
        })
        // 绘制图表
        myChart.setOption(this.options);
      }
    },
    props: {
      className: {
        type: String,
        default: "myChart"
      },
      options: {
        type: Object,
        default: () => {
          return {
            title: {text: '测试'},
            tooltip: {},
            xAxis: {
              data: ["衬衫", "羊毛衫", "雪纺衫", "裤子", "高跟鞋", "袜子"]
            },
            yAxis: {},
            series: [{
              name: '销量',
              type: 'line',
              data: [5, 20, 36, 10, 10, 20]
            }]
          }
        }
      },

    },
  }
</script>



